import React from 'react'
import "./sidebar.css"

function Sidebar() {
  return (
    <div className="sidebar">
        <div className="sidebar-item">
            <span className="sidebar-title">O TechNews</span>
            <p>
                TechNews to blog o nowinkach technologicznych i przeciekach. Skupiamy si na pisaniu o tematach z brany PC
                ktre s w zasigu rki kadego entuzjasty komputerowego, oraz staramy si przybliy tematy 
                ktre s bardziej odlege midziy innymi, wiat SBC i innych minikomputerw, wiat serwerw i wiat 
                wyspecjalizowanych stacji komputerowych oraz super komputerw.
            </p>
        </div>
        <div className="sidebar-item">
            <span className="sidebar-title">Posty</span>
            <ul className="sidebar-list">
                <li className="sidebar-list-item">Post 1</li>
                <li className="sidebar-list-item">Post 2</li>
                <li className="sidebar-list-item">Post 3</li>
                <li className="sidebar-list-item">Post 4</li>
                <li className="sidebar-list-item">Post 5</li>
                <li className="sidebar-list-item">Post 6</li>
                <li className="sidebar-list-item">Post 7</li>
                <li className="sidebar-list-item">Post 8</li>
                <li className="sidebar-list-item">Post 9</li>
                <li className="sidebar-list-item">Post 10</li>
            </ul>
        </div>
        <div className="sidebar-item">
            <span className="sidebar-title">Social media / Kontakt</span>
            <div className="sidebar-social">
                <i className="sidebarIcon fa-brands fa-youtube"></i>
                <i className="sidebarIcon fa-brands fa-telegram"></i>
                <i className="sidebarIcon fa-brands fa-facebook"></i>
            </div>
        </div>
        <div className="sidebar-item">
            <span className="sidebar-title">Lista twrcw i osb pomagajcych budowa TechNews</span>
            <ul className="sidebar-list">
                <li className="sidebar-list-item2">Twrca: Patryk "Whisky/PTFOPlayer"</li>
                <li className="sidebar-list-item2">Piszcy: Patryk "Whisky/PTFOPlayer"</li>
                <li className="sidebar-list-item2" style={{"margin-top": 15}}>Telegram: Micha "Wloseczeq"</li>
                <li className="sidebar-list-item2" style={{"margin-top": 15}}>Sprawdzanie poprawnoci i gramatyki:</li>
                <li className="sidebar-list-item2">Martyna "Maritrza"</li>
            </ul>
        </div>
    </div>
  )
}

export default Sidebar